$backgroundGradient: linear-gradient(
  180deg,
  rgba(255, 255, 255, 0.0620629) 0%,
  rgba(255, 255, 255, 0.0755026) 100%
);

.roundBox {
  width: 100%;
  padding: 0 30px;
  margin-bottom: 25px;
}
.roundContent {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 25px;
  gap: 25px;
}

.roundData {
  border-radius: 8px;
  padding: 10px;
  padding-top: 0;

  background: $backgroundGradient;
  border: 1px dashed rgba(72, 53, 234, 0.6);
  box-shadow: 0px 0px 24px rgba(72, 53, 234, 0.2);
}
.inputRoot {
  height: 35;

  background: $backgroundGradient;
  border: 1px dashed rgba(255, 255, 255, 0.1);
  border-radius: 8;
}
.inputFocus {
  border: 1px dashed rgba(255, 255, 255, 0.2) !important;
  box-shadow: 0px 0px 15px rgba(255, 255, 255, 0.1) !important;
}
.roundInput {
  color: white;
}
.roundLabel {
  font-family: Varela Round, sans-serif;
  font-size: 13px;
  line-height: 16px;
  color: white !important;
  opacity: 0.8;
}

.hiddenInput {
  position: absolute;
  opacity: 0;
  z-index: -2;
  right: 0;
  margin: 0;
  padding: 0;
}

.buttonWrapper {
  width: 100%;

  display: flex;
  justify-content: center;
  margin: 30px 0;
}

.button {
  width: 100%;
  max-width: 300px;
  min-height: 60px;

  border: 2px dashed #4835ea;
  box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.18);
  border-radius: 8px;

  background: transparent !important;
  transition: transform 0.4s;

  & * {
    font-size: 20px;
    line-height: 37px;

    text-align: center;
    text-transform: uppercase;
    color: #ffffff;
  }

  &:hover {
    transform: scale(1.01);
  }
}

.tableInnerWrapper {
  height: auto;
}
.table {
}
.tableHead {
}
.tableCell {
  border: none;
  font-weight: 400;
  font-size: 1rem;
  color: white;
  white-space: nowrap;
  padding: 16px 5px;

  @media (max-width: 600px) {
    padding: 10px 5px;
    font-size: 0.75rem;
  }
}
.tablebody {
}

.title {
  color: #ffffff;
  font-size: 22px;
  font-family: "Gilroy";
  font-weight: bold;
  line-height: 27px;
  margin-left: 30px;
}
.titleWrapper {
  width: 100%;
  height: 50px;
  margin: 15px 0;
}

.cellContentWrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.bLabel {
  display: inline;
  padding: 0.2em 0.6em 0.3em;
  font-size: 75%;
  font-weight: 700;
  line-height: 1;
  color: #fff;
  text-align: center;
  white-space: nowrap;
  vertical-align: baseline;
  border-radius: 0.25em;

  background: #777;
}

.bPrimary {
  background: #337ab7;
}

.bDanger {
  background: #d9534f;
}

.scrollBar {
  $header: 280;
  $footer: 120;
  $margin: 50;

  max-height: 500px;
  height: calc(100vh - #{$header+$footer+$margin}px) !important;
}
